:root[color-theme='light'] {
  --background: #fff;
  --boxColor: rgba(0,0,0,0.05);
  --box-boder-Color: #ddd;
  --textColor:#000;
  --info_img:url('icons/info_black.svg');
  --del_img:url('icons/event_repeat_black.svg');
  --rez_img:url('icons/event_black.svg');
}

:root[color-theme='dark'] {
  --background: #121212;
  --boxColor: rgba(255,255,255,0.05);
  --box-boder-Color: #0000;
  --textColor:#FAFAFA;
  --state:'black';
  --info_img:url('icons/info_white.svg');
  --del_img:url('icons/event_repeat_white.svg');
  --rez_img:url('icons/event_white.svg');
}
html {
  background: var(--background);
}
.App {
  background: var(--background);
}

#elementdiv:hover {
  box-shadow: 1px 1px 20px var(--box-boder-Color);
  background-color: var(--boxColor);
}
#elementdiv {
  width: 120px;
  height: 164px;
  border-radius: 10px;
}
#number-div {
  text-align: center;
  width: 100px;
  margin-left: 10px;
  color: var(--textColor);
}
#pic-tag {
  margin-left: 10px;
  margin-top: 10px;
}
.delete-tag {
  margin-left: 60px;
  margin-bottom: 10px;
  margin-right: 10px;
  content: var(--del_img);
}
.rezen-tag {
  margin-left: 60px;
  margin-bottom: 10px;
  margin-right: 10px;
  content: var(--rez_img);
}
.info_table {
  width: 300px;
  height: 300px;
  border-style: solid;
}
.info_table > tr > td {
  word-wrap:break-word;
  word-break:break-all;
}
.info_button {
  margin-left: 10px;
  margin-bottom: 10px;
  content: var(--info_img);
}
.tab {
  display: flex;
}
.modal-bg {width:100%;height:100%;position:fixed;top:0;left:0;right:0;background: rgba(0, 0, 0, 0.6);z-index:999;}
.modal-wrap {position: fixed;top:50%;left:50%;transform:translate(-50%,-50%);width:300px;height:300px;background:#fff;z-index:1000;}
.navbar-text
{
      line-height: 8px;
}
